@import './types';
h1{
    font-size: 80rem;
    letter-spacing: -2rem;
    line-height: 1.2;
}
h2{
    font-size: 60rem;
    word-break: keep-all;
}
h3{
    font-size: 40rem;
}
h4{
    font-size: 30rem ;
}
h5{
    font-size: 20rem;
}
h6{
    font-size: 16rem;
}
p{
    font-size: 20rem;
    word-break: keep-all;
}

.colorMain{
    color: $mainColor;
}

.btnType1{
    font-size: 20rem;
    font-weight: bold;
    padding: 15rem 40rem;
    &.hover1{
        border: 1px solid #fff;
        border-radius: 15rem;
        transition: all 0.3s;
        &:hover{
            background-color: #fff;
            color: $mainColor;
        }
    }
    &.hover2{
        border: 1px solid #202020;
        border-radius: 15rem;
        transition: all 0.3s;
        &:hover{
            background-color: $mainColor;
            border-color: $mainColor;
            color: #fff;
        }
    }
}

.btnType2{
    font-weight: bold;
    transition: all 0.3s;
    &:hover{
        color: $mainColor
    }
}

header{
    width: 100vw;
    padding: 10rem 0;
    position: fixed;
    z-index: 20;
    color: #fff;
    nav {
        a{
            margin-left: 20rem;
        }
    }
}

@media (max-width:1460px) and (min-width:1025px){
    html{
        font-size: 0.0684931506849315vw;
    }
}

@media (max-width:1024px) and (min-width:900px){
    html{
        font-size: 0.09765625vw;
        .center{
            padding: 0 50rem;
        }
        h3{
            font-size: 45rem;
        }
    }
}

@media (max-width:899px) and (min-width:768px){
    html{
        font-size: 0.1302083333333333vw;
        .center{
            padding: 0 50rem;
        }
        h2{
            font-size: 50rem;
        }
        h3{
            font-size: 45rem;
        }
        h4{
            font-size: 35rem;
        }
        p{
            font-size: 26rem;
        }
    }
}
@media (max-width:767px){
    html{
        font-size: 0.2415458937198068vw;
        .center{
            padding: 0 30rem;
        }
        h2{
            font-size: 48rem;
        }
    }
}