@import './types';
#mainHeader{
    background-color: $mainColor;
    color: $mainRvers;
    padding: 200rem 0;
    box-sizing: border-box;
    position: relative;
    background-image: url('../img/main_mask1.png');
    .center{
        position: relative;
        z-index: 1;
        align-items: flex-start;
        h2{
            margin-bottom: 30rem;
        }
        p{
            line-height: 1.4;
            margin-bottom: 40rem;
        }
    }
    img{
        position: absolute;
        bottom: 0;
        right: 0;
        opacity: 0.85;
        width: 500rem;
    }
}
#skill{
    padding: 120rem 0;
    .center{
        h3{
            margin-bottom: 120rem;
        }
        .jusSB{
            .column{
                h4{
                    font-weight: 600;
                    margin-bottom: 10rem;
                }
                ul{
                    li{
                        margin-top: 10rem;
                        margin-left: 20rem;
                        list-style: circle;
                    }
                }
            }
        }
    }
}
#works{
    padding: 120rem 0;
    background-color: $subColor;
}
#contact{
    background-color: #f0f0f0;
    padding: 120rem 0;
    .center{
        .title{
            padding-right: 30rem;
            h3{
                margin-bottom: 20rem;
            }
            h5{
                line-height: 1.4;
            }
        }
        .flex{
            a{  
                font-size: 0;
                margin-right: 20rem;
                svg{
                    width: 80rem;
                }
            }
        }
    }
}

@media (max-width:1024px) and (min-width:900px){
    #mainHeader{
        padding: 100rem 0 0;
        min-height: 100vh;
    }
    #skill {
        padding-bottom: 80rem;
        .center{
            h3{
                margin-bottom: 70rem;
            }
            .jusSB{
                flex-flow: wrap;
                .column{
                    width: 50%;
                    margin-bottom: 40rem;
                }
            }
        }
    }
}

@media (max-width:899px) and (min-width:768px){
    #mainHeader{
        padding: 100rem 0 0;
        min-height: 100vh;
    }
    #skill {
        padding-bottom: 90rem;
        .center{
            h3{
                margin-bottom: 60rem;
            }
            .jusSB{
                flex-flow: wrap;
                .column{
                    width: 50%;
                    margin-bottom: 40rem;
                    ul{
                        li{
                            font-size: 18rem;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width:767px){
    #mainHeader{
        padding: 80rem 0 0;
        min-height: 100vh;
        img{
            width: 300rem;
        }
    }
    #works{
        padding-bottom: 60rem;
    }
    #skill{
        padding-bottom: 90rem;
        .center{
            h3{
                margin-bottom: 50rem;
            }
            .jusSB{
                flex-direction: column;
                .column{
                    margin-bottom: 30rem;
                    h4{
                        margin-bottom: 0;
                    }
                    ul{
                        li{
                            font-size: 20rem;
                        }
                    }
                }
            }
        }
    }
    #contact{
        .center{
            flex-direction: column;
            align-items: flex-start;
            .flex{
                padding-top: 30rem;
            }
        }
    }

}
