/* base */
html{
  font-size:6.25%;
}

body {
  font-size: 16rem;
  margin: 0;
  padding: 0;
  font-family: 'Pretendard', 'Noto Sans KR', 'Open Sans', sans-serif;
}

input, select, option, textarea,button {
  font-family: 'Pretendard', 'Noto Sans KR', 'Open Sans', sans-serif;
  box-sizing: border-box;
  appearance: none;
  resize: none;
  outline: none;
  border: none;
}

a {
text-decoration: none;
display: inline-block;
color: inherit;
}

label{
cursor: pointer;
}

ul, li, dl, dt, dd {
list-style: none;
margin: 0;
padding: 0;
}

i {
font-style: normal;
}

h1, h2, h3, h4, h5, h6, p {
margin: 0;
padding: 0;
}

header, footer, section{
  display: flex;
  justify-content: center;
}


.center {
width: 100%;
max-width: 1280rem;
display: flex;
}

.ab {
position: absolute;
}

.rel{
  position: relative;
}

.textRight {
text-align: right;
}

.textCenter {
text-align: center;
}

.textLeft {
text-align: left;
}

.fw500{
  font-weight: 500;
}

.fw600{
  font-weight: 600;
}

.fwBold{
  font-weight: bold;
}
.flex{
  display: flex;
}
.column {
  display: flex;
  flex-direction: column;
}

.jusSB{
  display: flex;
  justify-content: space-between;
}

.jusCenter{
  display: flex;
  justify-content: center;
}

.aliCenter{
  display: flex;
  align-items: center;
}

.table {
display: table;
}

.cell {
display: table-cell;
}

.row {
display: table-row;
}
